import React from 'react'

export default class AddNewRequest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      region: '',
      changedCategory: null
    }
    this.handleRegionInput = this.handleRegionInput.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleRadioButtonCheck = this.handleRadioButtonCheck.bind(this)
  }

  handleSubmit(evt) {
    evt.preventDefault()
    if (this.state.region.length <= 5 || this.state.changedCategory === null) {

    } else {
      this.props.addCategory(this.state.changedCategory)
      this.props.addRegion(this.state.region)
      this.props.openAddPictureScreen()
    }
  }

  handleRegionInput(evt) {
    this.setState({
      region: evt.target.value
    })
  }

  handleRadioButtonCheck() {
    const radios = document.getElementsByName('category');
    for (let i = 0, length = radios.length; i < length; i++) {
      if (radios[i].checked) {
        this.setState({
          changedCategory: radios[i].value
        })
        break;
      }
    }
  }

  render() {
    return (
      <form name="add-request" className="request-form">
        <button type="button" onClick={this.props.onClose} className="request-form__close-button close-button"/>
        <p className="request-form__title">Введите адрес</p>
        <input type="text"
               minLength="5"
               autoFocus
               placeholder={this.props.region !== null ? this.props.region : null}
               onChange={this.handleRegionInput}
               className="request-form__input"
               required/>
        <div className="request-form__category-box">
          <p className="request-form__title">Выберите категорию</p>
          <ul className="request-form__radio-container">
            <li className="request-form__radio-container-item">
              <label htmlFor="1">Дороги</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     required
                     type="radio"
                     value="Дороги"
                     id="1"
                     className="request-form__radio-item"/>
            </li>
            <li className="request-form__radio-container-item">
              <label htmlFor="2">Дураки</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="Дураки"
                     id="2"
                     className="request-form__radio-item"/>
            </li>
            <li className="request-form__radio-container-item">
              <label htmlFor="3">Что делать</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="Что делать"
                     id='3'
                     className="request-form__radio-item"/></li>
            <li className="request-form__radio-container-item">
              <label htmlFor="4">Улица, фонарь</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="Улица, фонарь"
                     id="4"
                     className="request-form__radio-item"/></li>
            <li className="request-form__radio-container-item">
              <label htmlFor="5">До водки охотники</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="До водки охотники"
                     id='5'
                     className="request-form__radio-item"/></li>
            <li className="request-form__radio-container-item">
              <label htmlFor="6">Документы-бумажки</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="Документы-бумажки"
                     id="6"
                     className="request-form__radio-item"/></li>
            <li className="request-form__radio-container-item">
              <label htmlFor="7">Братья наши меньшие</label>
              <input name="category"
                     onChange={this.handleRadioButtonCheck}
                     type="radio"
                     value="Братья наши меньшие"
                     id="7"
                     className="request-form__radio-item"/></li>
          </ul>
        </div>

        <button type="submit" onClick={this.handleSubmit} className="request-form__submit-button">Продолжить</button>
      </form>
    )
  }
}