import React from 'react'
import Card from '../Card/Card'

export class Main extends React.Component {
  constructor(props) {
    super(props);
    this.render = this.render.bind(this)
  }

  render() {
    if (this.props.addRegion !== null) {
      return (
        <div className="main">
          <button onClick={this.props.openAddNewRequest} className="main__add-request"/>
          <div className="main-quote"/>
          <p className="main__quote">Даже за легкий «фак» в эфире нас отфакают по полной.<br/>
            Рок-волна (The Boat That Rocked)</p>
          <div>
            <div className="request-filter">
              <p>Все обращения <span onClick={this.props.openAddressScreen}
                                     className="request-filter__location">{this.props.addRegion}</span>
              </p>
            </div>
            <div className="elements">
              <Card/>
              <Card/>
              <Card/>
              <Card/>
            </div>
          </div>
        </div>)
    } else {
      return (
        <div className="main">
          <button onClick={this.props.openAddNewRequest} className="main__add-request"/>
          <div className="main-quote"/>
          <p className="main__quote">Даже за легкий «фак» в эфире нас отфакают по полной.<br/>
            Рок-волна (The Boat That Rocked)</p>
          <div className="request-filter">
            <p>Для отображения списка карточек выберите <span onClick={this.props.openAddressScreen} className="request-filter__location">Регион</span>
            </p>
          </div>
        </div>)
    }
  }
}